:root {
  --font-body: "Lato", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --brown: hsl(29, 85%, 35%);
  --brown-light:hsla(29, 85%, 35%, 0.17);
  --brown-lighter:hsla(29, 85%, 35%, 0.37);
  --brown-dark:hsla(30, 77%, 36%, 1);
  --brown-darker: hsla(29, 84%, 25%, 1);
  --pink-light:hsla(29, 82%, 95%, 1);
  --brown-light:hsla(28, 91%, 23%, 0.85);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
