  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.banner__container > div {
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 0 70px 0;
  opacity: 0;
  transition: opacity 3s;
}
.banner__container > div.inactive {
  opacity: 0;
}
.banner__container > div.inactive .banner__title {
  animation: text-out-left 2.5s ease-in-out forwards;
}
.banner__container > div.inactive .banner__button {
  animation: text-out-right 2.5s ease-in-out forwards;
}
.banner__container > div.inactive .banner__text {
  opacity: 0;
  transition: opacity 3s;
  visibility: hidden;
  pointer-events: none;
}
.banner__container > div.active {
  position: relative;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}
.banner__container > div.active .banner__title {
  animation: text-in-left 2.5s ease-in-out forwards;
}
.banner__container > div.active .banner__button {
  animation: text-in-right 2.5s ease-in-out forwards;
}
.banner__description {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  justify-items: center;
}
@media (max-width: 59.375em) {
