}
@keyframes text-in-left {
  from {
    opacity: 0;
    transform: translateX(-500%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes text-in-right {
  from {
    opacity: 0;
    transform: translateX(500%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes text-out-left {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-500%);
  }
}
@keyframes text-out-right {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(500%);
  }
}

