  height: 3.4rem;
  width: 3.4rem;
  position: fixed;
  top: 3.5rem;
  right: 4rem;
  border-radius: 50%;
  z-index: 2000;
  text-align: center;
  cursor: pointer;
}
@media (max-width: 39.9375em) {
  .navigation__button {
    right: 1rem;
    top: 1.5rem;
  }
}
.navigation__background {
  height: 3.4rem;
  width: 3.4rem;
  border-radius: 50%;
  position: fixed;
  top: 3.5rem;
  right: 4rem;
  background-image: radial-gradient(var(--brown), var(--brown-dark));
  z-index: 1000;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
